<template>
  <div class="home">
    <div class="search">
    <input v-model="search" v-on:keyup.enter="updateCountry(search)" placeholder="search a country"/><i class="fas fa-search"></i>
    </div>
    <!--<countryCardIndex img="https://flagcdn.com/w320/pe.png" name="Peru" region="Americas" subregion="South America"/>-->
    <countryCard v-if="showDetails" :img="img" :name="name" :independent="independent" :lang="lang" :currency="currency" :continent="continent" :region="region" :subregion="subregion" :population="population" :timezone="timezone"/>
    <h4 v-if="clickCountryPrompt && load==false" class="prompt">CLICK A COUNTRY</h4>
    <h1 v-if="load" aria-busy="true" class="load"></h1>
    <div class="grid1">
      <countryCardIndex v-for="(country) in allcountries" :img="country.flags.png" :name="country.name.common" :region="country.region" :subregion="country.subregion" :key="country.altSpellings[0]" @click="loadDetails(country.flags.png, country.name.common, country.independent, country.languages[Object.keys(country.languages)[0]],country.currencies[Object.keys(country.currencies)[0]].name,country.continents[0], country.region, country.subregion, country.population, country.timezones[0])"/>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import countryCard from '@/components/countryCard.vue'
import countryCardIndex from '@/components/countryCardIndex.vue'
import countrymon from '../countrymon'

export default {
  name: 'HomeView',
  data(){
    return{
      allcountries:[],
      load:true,
      search:"",
      showDetails:false,
      clickCountryPrompt:true,
      img:"",
      name:"",
      independent:false,
      lang:"",
      currency:"",
      continent:"",
      region:"",
      subregion:"",
      population:0,
      timezone:""
    }
  },
  components: {
    countryCard,countryCardIndex
  },
  async created(){
    this.allcountries = await countrymon.getCountry()
    this.load = false
  },

  methods:{
    loadDetails(img,name,indepence,lang,currency,continent,region,subregion,population,timezone){
      this.img = img
      this.name = name
      this.independent = indepence
      this.lang = lang
      this.currency = currency
      this.continent = continent
      this.region = region
      this.subregion = subregion
      this.population = population
      this.timezone = timezone
      this.showDetails = true
      this.clickCountryPrompt = false
    },
    async updateCountry(countryName){
      this.allcountries = []
      this.load = true
      this.allcountries = await countrymon.getCountry(countryName)
      this.load = false
    }
  }
}
</script>

<style scoped>
.load{
  z-index: 2;
  left: 50%;
  display: fixed;
  margin-top: 150px;
}
.prompt{
  height: 100%;
  left: 65%;
  top: 50%;
  z-index: 1;
  width: 500px;
  position: fixed;
  color: grey;
}
.search{
  width: 400px;
  display: flex;
  left: 40%;
  top: 10%;
  position: fixed;
}

.fas{
  margin-top: 20px;
  margin-left: 20px;

}
</style>