const countryurl = "api/country"
const bookmarkurl = "api/bookmark"

const getCountry = async(name = null)=>{
    if(name != null){
        try{
            const getAll = await fetch(countryurl+`/${name}`)
            const res = await getAll.json()
            return res.map(country=>({
                ...country
            }))
        }catch(err){console.error(err)}
    }else{
        try{
            const getAll = await fetch(countryurl)
            const res = await getAll.json()
            return res.map(country=>({
                ...country
            }))
        }catch(err){console.error(err)}
    }
}

const getCountryByLang = async (lang)=>{
    try{
        const getAll = await fetch(countryurl+`/lang/${lang}`)
        const res = await getAll.json()
        return res.map(language=>({
            ...language
        }))
    }catch(err){console.error(err)}
}

const getCountryByCurrency = async (curr)=>{
    try{
        const getAll = await fetch(countryurl+`/currency/${curr}`)
        const res = await getAll.json()
        return res.map(currency=>({
            ...currency
        }))
    }catch(err){console.error(err)}
}

const getBookmarks = async ()=>{
    try{
        const getAll = await fetch(bookmarkurl)
        const res = await getAll.json()
        return res.map(book=>({
            ...book
        }))
    }catch(err){console.error(err)}
}

const addBookmarks = async (countryImg,countryName,independent,countryLanguage,countryCurrency,countryContinent,countryRegion,countrySubregion,countryPopulation,countryTimezone)=>{
    try{
        const bookmarkData = {
            countryImg,countryName,independent,countryLanguage,countryCurrency,countryContinent,countryRegion,countrySubregion,countryPopulation,countryTimezone
        }
        var myHeaders = new Headers()
        myHeaders.append("Content-Type", "application/json")
        var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: JSON.stringify(bookmarkData),
            redirect: 'follow'
        }
          
        await fetch(bookmarkurl, requestOptions)
        return 
    }catch(err){console.error(err)}
   
}


const deleteBookmarks = async (id)=>{
    try{
        await fetch(bookmarkurl+`/${id}`, {
            method: 'DELETE'
        })
        return
    }catch(err){console.error(err)}
}

const countrymon = {
    getCountry,
    getCountryByLang,
    getCountryByCurrency,
    getBookmarks,
    addBookmarks,
    deleteBookmarks
}

export default countrymon